import React, { PureComponent } from "react";

export default class TopTicker extends PureComponent {
  render() {
    const { CustomTicker, UgcSettings } = this.props;
    const { title, status, ticker_position, tickerType, ticker_height, ticker_speed, subtitle_css_font, title_font_color, subTitle_font_color, subTitle_font_size, subTitle, subtitle_font_varient, subTitle_backgroud_color, title_backgroud_color, title_font_size, title_font_varient, title_css_font } = CustomTicker;
    const ugcStyle = (UgcSettings && UgcSettings.onsite_status) && UgcSettings.onsite_overlay_poisition == "1" ? { marginTop: `${UgcSettings.onsite_overlay_height}px` } : {}
    return (
      <div id="wtickerTop" className="headerTicker tickerTop" style={{ display: status === 1 && ticker_position === "1" ? '' : "none", ...ugcStyle }}>
        <div className={`tickerTitleView ${tickerType === 3 ? 'ticker3titleView' : ''}`} style={{ display: tickerType == 2 ? "none" : "flex", backgroundColor: title_backgroud_color, height: tickerType == 1 ? ticker_height : (ticker_height - 10) < 0 ? ticker_height : (ticker_height - 10) }}>
          <h1 className={`setFont sGFfonte-${title_font_varient}`} style={{ fontFamily: title_css_font, fontWeight: 500, fontSize: (ticker_height < 40 && title_font_size > 24 ) ? 20 : title_font_size, color: title_font_color }}>{title}</h1>
        </div>

        <div className="tickerSubtitleView" style={{ backgroundColor: subTitle_backgroud_color, height: ticker_height, display: status === 1 ? '' : "none" }}>
          <marquee scrollamount={ticker_speed === 5 ? 5 : ticker_speed === 10 ? 10 : 20}>
            <h2 className={`setFont sGFfonte-${subtitle_font_varient}`} style={{ fontFamily: subtitle_css_font, fontSize: (ticker_height < 40 && subTitle_font_size> 24 ) ? 20 : subTitle_font_size, color: subTitle_font_color }}>
              {subTitle}
            </h2>
          </marquee>
        </div>
      </div>
    )
  }
}